@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800;900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
  background: #000000;
  min-height: 100vh;
  margin: 0;
  color: #ffffff;
  overflow-x: hidden;
}

.phone-container {
  width: 100%;
  max-width: 600px;
  height: 100vh;
  background: #000000;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

/* 헤더 스타일 */
.header {
  background: linear-gradient(135deg, #000000 0%, #1a1a1a 100%);
  padding: 24px;
  text-align: center;
  border-bottom: 1px solid #333;
}

.app-title {
  font-size: 32px;
  font-weight: 900;
  color: #ffffff;
  margin-bottom: 8px;
  letter-spacing: -1px;
  text-transform: uppercase;
}

.subtitle {
  font-size: 14px;
  color: #888;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 2px;
}

/* Chad 레벨 스타일 */
.chad-level {
  background: linear-gradient(90deg, #333, #555);
  padding: 16px 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #333;
}

.level-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.level-title {
  font-size: 18px;
  font-weight: 800;
  color: #ffffff;
}

.level-progress {
  flex: 1;
  margin: 0 16px;
}

.progress-bar {
  width: 100%;
  height: 8px;
  background: #333;
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 4px;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #666, #999);
  border-radius: 4px;
  transition: width 0.8s ease;
}

.progress-text {
  font-size: 11px;
  color: #888;
  text-align: center;
}

.chad-avatar {
  width: 60px;
  height: 60px;
  background: linear-gradient(135deg, #333, #555);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  border: 2px solid #666;
}

/* 메인 영역 스타일 */
.main-area {
  flex: 1;
  padding: 24px;
  overflow-y: auto;
  background: #0a0a0a;
}

/* Chad 메시지 스타일 */
.chad-message {
  background: linear-gradient(135deg, #1a1a1a, #2d2d2d);
  border: 1px solid #444;
  border-radius: 16px;
  padding: 20px;
  margin-bottom: 24px;
  position: relative;
}

.chad-message:before {
  content: '';
  position: absolute;
  top: -8px;
  left: 24px;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid #444;
}

.message-text {
  font-size: 16px;
  font-weight: 600;
  color: #ffffff;
  line-height: 1.4;
  text-align: center;
}

.message-subtext {
  font-size: 12px;
  color: #888;
  text-align: center;
  margin-top: 8px;
  font-style: italic;
}

/* 섹션 제목 */
.section-title {
  font-size: 20px;
  font-weight: 800;
  color: #ffffff;
  margin-bottom: 16px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* 스크롤바 스타일링 */
.main-area::-webkit-scrollbar {
  width: 4px;
}

.main-area::-webkit-scrollbar-track {
  background: #111;
}

.main-area::-webkit-scrollbar-thumb {
  background: #333;
  border-radius: 2px;
}

.main-area::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* 동기부여 인용구 */
.motivational-quotes {
  text-align: center;
  opacity: 0.5;
  margin-top: 32px;
  padding-bottom: 20px;
}

.quote-text {
  font-size: 14px;
  color: #888;
  font-style: italic;
  font-weight: 500;
}
