/* 태스크 리스트 스타일 */
.task-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.task-item {
  background: linear-gradient(135deg, #111, #222);
  border: 1px solid #333;
  border-radius: 12px;
  padding: 20px;
  transition: all 0.3s ease;
  cursor: pointer;
  position: relative;
}

.task-item:hover {
  border-color: #555;
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(255, 255, 255, 0.1);
}

.task-item.current {
  border: 2px solid #666;
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.2);
}

.task-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.task-priority {
  padding: 4px 12px;
  border-radius: 8px;
  font-size: 11px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.priority-alpha {
  background: #333;
  color: #fff;
  border: 1px solid #555;
}

.priority-beta {
  background: #222;
  color: #888;
  border: 1px solid #444;
}

.task-title {
  font-size: 18px;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 8px;
}

.task-description {
  font-size: 14px;
  color: #aaa;
  line-height: 1.4;
  margin-bottom: 16px;
}

.task-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.task-deadline {
  font-size: 12px;
  color: #888;
  font-weight: 500;
}

.chad-buttons {
  display: flex;
  gap: 12px;
}

.chad-btn {
  padding: 10px 20px;
  border: 2px solid;
  border-radius: 8px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 800;
  cursor: pointer;
  transition: all 0.2s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.btn-yes {
  background: linear-gradient(135deg, #333, #555);
  color: #ffffff;
  border-color: #666;
}

.btn-yes:hover {
  background: linear-gradient(135deg, #555, #777);
  transform: scale(1.05);
  box-shadow: 0 4px 15px rgba(255, 255, 255, 0.2);
}

.btn-no {
  background: transparent;
  color: #888;
  border-color: #444;
}

.btn-no:hover {
  background: #222;
  color: #aaa;
  border-color: #555;
}
