/* Chad 반응 모달 */
.chad-reaction {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.95);
  border: 2px solid #666;
  border-radius: 16px;
  padding: 32px;
  text-align: center;
  z-index: 1000;
  opacity: 0;
  transition: all 0.3s ease;
  max-width: 300px;
  pointer-events: none;
}

.chad-reaction.show {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1.05);
  pointer-events: auto;
}

.reaction-text {
  font-size: 20px;
  font-weight: 800;
  color: #ffffff;
  margin-bottom: 8px;
}

.reaction-subtext {
  font-size: 14px;
  color: #888;
}

/* 태스크 추가 모달 */
.add-task-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
  z-index: 2000;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}

.add-task-modal.show {
  opacity: 1;
  visibility: visible;
}

.modal-content {
  background: linear-gradient(135deg, #111, #222);
  border: 2px solid #555;
  border-radius: 16px;
  padding: 40px;
  width: 95%;
  max-width: 400px;
  max-height: 90vh;
  overflow-y: auto;
}

.modal-title {
  font-size: 20px;
  font-weight: 800;
  color: #ffffff;
  text-align: center;
  margin-bottom: 8px;
  text-transform: uppercase;
}

.modal-subtitle {
  font-size: 12px;
  color: #888;
  text-align: center;
  margin-bottom: 24px;
  font-style: italic;
}

.form-group {
  margin-bottom: 20px;
}

.form-label {
  font-size: 12px;
  color: #888;
  margin-bottom: 8px;
  display: block;
  text-transform: uppercase;
  font-weight: 600;
}

.form-input {
  width: 100%;
  background: #000;
  border: 1px solid #444;
  border-radius: 8px;
  padding: 12px;
  color: #ffffff;
  font-family: inherit;
  font-size: 14px;
  font-weight: 500;
}

.form-input:focus {
  outline: none;
  border-color: #666;
  box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1);
}

.form-textarea {
  resize: none;
  min-height: 80px;
}

.time-select,
.priority-select {
  display: flex;
  gap: 8px;
  margin-top: 8px;
}

.time-option,
.priority-option {
  flex: 1;
  padding: 8px;
  border: 1px solid #444;
  border-radius: 8px;
  background: transparent;
  color: #888;
  font-size: 11px;
  font-weight: 600;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.2s ease;
}

.time-option.selected,
.priority-option.selected {
  border-color: #666;
  background: #222;
  color: #ffffff;
}

.modal-actions {
  display: flex;
  gap: 12px;
  margin-top: 24px;
}

.modal-btn {
  flex: 1;
  padding: 12px;
  border: 2px solid;
  border-radius: 8px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 800;
  cursor: pointer;
  transition: all 0.2s ease;
  text-transform: uppercase;
}

.btn-create {
  background: linear-gradient(135deg, #333, #555);
  color: #ffffff;
  border-color: #666;
}

.btn-create:hover {
  background: linear-gradient(135deg, #555, #777);
  transform: translateY(-1px);
}

.btn-cancel {
  background: transparent;
  color: #888;
  border-color: #444;
}

.btn-cancel:hover {
  background: #222;
  color: #aaa;
}
