/* 하단 네비게이션 */
.bottom-nav {
  height: 88px;
  background: #000000;
  border-top: 1px solid #333;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0 24px;
}

.nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
  color: #666;
  font-size: 12px;
  font-weight: 700;
  transition: all 0.2s;
  padding: 8px 16px;
  border-radius: 8px;
  text-transform: uppercase;
  cursor: pointer;
}

.nav-item:hover {
  color: #aaa;
  background: #111;
}

.nav-item.active {
  color: #ffffff;
  background: #222;
}

.nav-icon {
  font-size: 18px;
  margin-bottom: 4px;
}
